@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {

    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    padding-top: 5rem;

    .notavif.notwebp & {
        background-image: linear-gradient( v.$secundarioOpacity 0%, v.$secundarioOpacity 100%), 
        url(../img/header.jpg);
    }
    
    .webp & {
        background-image: linear-gradient( v.$secundarioOpacity 0%, v.$secundarioOpacity 100%), 
        url(../img/header.webp);
    }
    
    .avif & {
        background-image: linear-gradient( v.$secundarioOpacity 0%, v.$secundarioOpacity 100%), 
        url(../img/header.avif);   
    }
    
    &__logo {
        max-width: 22rem;
        width: 20rem;
        margin: 0 auto;
        @include m.tablet {
            max-width: 20rem;
        }
        
        @include m.tablet {
            margin: 0;
        }
    }

    &__contenido {
        padding: 6rem 0 6rem 0;
    }

    &__heading {
        color: v.$blanco;
        font-size: 4rem;
        text-align: center;
        max-width: 50%;
        line-height: 1.1;
        margin: 0 auto 6rem auto;
        font-size: 3rem;

        @include m.tablet {
            max-width: 40%;
            font-size: 4.5rem;
            text-align: left;
            margin: 0 0 6rem 0;
        }

        @include m.desktop {
            font-size: 5rem;
        }
    }

    &__link {
        @include m.boton(v.$primario, v.$secundario);
        max-width: 50%;
        margin: 0 auto;
    }
}