@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$secundario;
    padding-top: 5rem;

    &__grid {
        @include m.tablet {
            @include m.grid(4, 4rem);
        }
    }
    
    &__logo {
        max-width: 15rem;
        margin: 0 auto 3rem auto;

        @include m.tablet {
            width: 80%;
        }
    }
    
    &__heading {
        font-size: 2.2rem;
        color: v.$blanco;
        text-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.582);
        margin-bottom: 1rem;

        @include m.tablet {
            text-align:left;
            margin-bottom: 3rem;
        }
    }

    &__box {
        margin-bottom: 4rem;

        &:last-of-type {
            margin-bottom: 0;
        }
    }
    
    &__link {
        display: block;
        color: v.$blanco;
        font-size: 2rem;
        text-align: center;
        margin-bottom: 1rem;

        &:last-of-type {
            margin-bottom: 0;
        }

        @include m.tablet {
            text-align: left;
        }
    }

    &__copyright {
        margin: 0;
        color: v.$blanco;
        font-size: 1.3rem;
        text-align: center;
        border-top: .5rem solid v.$primario;
        padding: 1rem 0;
        margin: 2rem auto 0 auto;
        max-width: 120rem;
    }
}