@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimoniales {
    padding: 8rem 0;
    
    &__grid {
        @include m.tablet {
            @include m.grid(5, 4rem);
            align-items: center;
    }
    }
    
    &__imagen-contenedor {
        grid-column: 1 / 3;
    }

    &__imagen {
        margin: 0 auto;
        @include m.tablet {
            margin: 0;
        }
    }
    
    &__contenido {
        grid-column: 3 / 6;
        margin-top: 5rem;
        @include m.tablet {
            margin-top: 0;
        }
    }

    &__heading {
        margin-bottom: 5rem;
        
        text-align: center;
        @include m.tablet {
            text-align: left;
        }
        &--span {
            @include m.headingBorder(v.$secundario, v.$primario);
        }
    }

    &__texto {
        border: .5rem solid v.$secundario;
        border-radius: 1rem;
        margin: 0;
        margin-bottom: 3rem;
        padding: 2rem 0 2rem 6rem;
        background-color: v.$primario;
        position: relative;

        @include m.tablet {
            max-width: 70%;
        }
        
        &:last-of-type {
            margin-bottom: 0;
        }

        &::before {
            content: '';
            display: block;
            height: 5rem;
            width: 5rem;
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            position: absolute;
            left: .5rem;
            top: 3rem;
        }

        @include m.tablet {
            
        &:first-of-type {
            margin-left: 10%;
        }

        &:nth-child(3) {
            margin-left: 20%;
        }

        &:nth-child(4) {
            margin-left: 30%;
        }
        }
        
    }
}