@use 'base/variables' as v;
@use 'base/mixins' as m;

.beneficios {
    padding: 5rem 0;
    background-color: v.$primario;
    
    @include m.desktop {
        padding: 8rem 0;
    }

    &__grid {
        @include m.tablet {
            @include m.grid(5, 4rem);
            align-items: center;
        }        
    }
    &__contenido {
        grid-column: 1 / 4;
    }

    &__heading {
        text-align: center;
        padding: 0 2rem;

        
        &--span {
            @include m.headingBorder(v.$secundario, v.$blanco);
            margin-left: .6rem;
        }
        
        
        @include m.desktop {
            text-align: left;
            padding: 0;
        }
    }

    &__texto {
        text-align: center;
        padding: 0 2rem;
        margin-bottom: 5rem;

        @include m.tablet {
            padding: 0 1.8rem 0 0;
        }
            
        @include m.desktop {
            padding: 0 2rem 0 0;
            text-align: left;
            margin: 0;
            
        }
    }
    
    &__imagen {
        grid-column: 4 / 6;
        transition: transform 300ms ease-in-out;

        &:hover {
            transform: scale(1.1) rotate(8deg);
        }
    }
}