@use 'base/variables' as v;
@use 'base/mixins' as m;

.planes {
    background-color: v.$secundario;
    padding: 8rem 0;
    text-align: center;

    @include m.tablet {
        text-align: left;
    }

    &__heading {
        margin: 0 auto;

        &--span {
            @include m.headingBorder(v.$blanco, v.$primario);
        }

    }

    &__descripcion {
        color: v.$blanco;
        padding: 0 6rem;
        margin-bottom: 7rem;

        @include m.tablet {
            padding: 0;
        }

    }

    &__flex {
        display: flex;
        flex-direction: column;

        @include m.tablet {
            flex-direction: row;
            justify-content: center;
            
        }
    }
}

.plan {
    background-color: v.$blanco;
    flex: 1;
    padding: 16rem 4rem 4rem 4rem;
    border-bottom: .1rem solid #494545;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    overflow: hidden;
    
    @include m.tablet {
        border: none;
        transition: transform 300ms ease-in-out;
        
        &:hover {
            transform: scale(1.1);    
        }
    }
    
    &__precio {
        text-align: center;
        font-weight: 700;
        font-size: 2.3rem;
    }
    
    &__listado {
        margin-bottom: 3rem;
    }
    
    &__opciones {
        margin-bottom: 2rem;
        text-align: center;
        font-size: 1.9rem;
    }
    
    &__boton {
        @include m.boton(v.$secundario, v.$primario);

        @include m.tablet {
            display: block;
        }
    }   
    
    &.negocio {
        order: -1;
        background-color: v.$primario;
        border-radius: 1rem;
        z-index: 10;
        
        @include m.bgIcon('../img/icono_negocio.svg');
    
        @include m.tablet {
            order: unset;
            margin: -5rem 0;
        }
        
    }

    &.inicio {
        @include m.bgIcon('../img/icono_inicio.svg');
    }
    
    &.pro {
        @include m.bgIcon('../img/icono_pro.svg');
    }
}