.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #84d9bc;
}

.margin-center {
  margin: 0 auto;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.header {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding-top: 5rem;
}
.notavif.notwebp .header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url(../img/header.jpg);
}
.webp .header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url(../img/header.webp);
}
.avif .header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url(../img/header.avif);
}
.header__logo {
  max-width: 22rem;
  width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo {
    max-width: 20rem;
  }
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}
.header__contenido {
  padding: 6rem 0 6rem 0;
}
.header__heading {
  color: #FFFFFF;
  font-size: 4rem;
  text-align: center;
  max-width: 50%;
  line-height: 1.1;
  margin: 0 auto 6rem auto;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .header__heading {
    max-width: 40%;
    font-size: 4.5rem;
    text-align: left;
    margin: 0 0 6rem 0;
  }
}
@media (min-width: 1024px) {
  .header__heading {
    font-size: 5rem;
  }
}
.header__link {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  max-width: 50%;
  margin: 0 auto;
}
.header__link:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .header__link {
    width: auto;
    display: inline-block;
  }
}

.crea {
  padding: 5rem 0;
}
@media (min-width: 1024px) {
  .crea {
    padding: 8rem 0;
  }
}
@media (min-width: 768px) {
  .crea__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.crea__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.crea__imagen {
  width: 80%;
  margin: 0 auto 2rem auto;
}
.crea__heading {
  font-size: 3.6rem;
  display: inline-block;
  color: #313c4e;
}
.crea__heading::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin-top: -1.4rem;
}
.crea__texto {
  text-align: center;
  padding: 0 3rem;
}

.beneficios {
  padding: 5rem 0;
  background-color: #84d9bc;
}
@media (min-width: 1024px) {
  .beneficios {
    padding: 8rem 0;
  }
}
@media (min-width: 768px) {
  .beneficios__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.beneficios__contenido {
  grid-column: 1/4;
}
.beneficios__heading {
  text-align: center;
  padding: 0 2rem;
}
.beneficios__heading--span {
  font-size: 3.6rem;
  display: inline-block;
  color: #313c4e;
  margin-left: 0.6rem;
}
.beneficios__heading--span::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin-top: -1.4rem;
}
@media (min-width: 1024px) {
  .beneficios__heading {
    text-align: left;
    padding: 0;
  }
}
.beneficios__texto {
  text-align: center;
  padding: 0 2rem;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .beneficios__texto {
    padding: 0 1.8rem 0 0;
  }
}
@media (min-width: 1024px) {
  .beneficios__texto {
    padding: 0 2rem 0 0;
    text-align: left;
    margin: 0;
  }
}
.beneficios__imagen {
  grid-column: 4/6;
  transition: transform 300ms ease-in-out;
}
.beneficios__imagen:hover {
  transform: scale(1.1) rotate(8deg);
}

.estadisticas {
  margin: 8rem 0;
}
.estadisticas__heading {
  text-align: center;
}
.estadisticas__heading--span {
  font-size: 3.6rem;
  display: inline-block;
  color: #313c4e;
}
.estadisticas__heading--span::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin-top: -1.4rem;
}
@media (min-width: 768px) {
  .estadisticas__heading--span {
    margin-left: 0.8rem;
  }
}

.planes {
  background-color: #313c4e;
  padding: 8rem 0;
  text-align: center;
}
@media (min-width: 768px) {
  .planes {
    text-align: left;
  }
}
.planes__heading {
  margin: 0 auto;
}
.planes__heading--span {
  font-size: 3.6rem;
  display: inline-block;
  color: #FFFFFF;
}
.planes__heading--span::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin-top: -1.4rem;
}
.planes__descripcion {
  color: #FFFFFF;
  padding: 0 6rem;
  margin-bottom: 7rem;
}
@media (min-width: 768px) {
  .planes__descripcion {
    padding: 0;
  }
}
.planes__flex {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .planes__flex {
    flex-direction: row;
    justify-content: center;
  }
}

.plan {
  background-color: #FFFFFF;
  flex: 1;
  padding: 16rem 4rem 4rem 4rem;
  border-bottom: 0.1rem solid #494545;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  overflow: hidden;
}
@media (min-width: 768px) {
  .plan {
    border: none;
    transition: transform 300ms ease-in-out;
  }
  .plan:hover {
    transform: scale(1.1);
  }
}
.plan__precio {
  text-align: center;
  font-weight: 700;
  font-size: 2.3rem;
}
.plan__listado {
  margin-bottom: 3rem;
}
.plan__opciones {
  margin-bottom: 2rem;
  text-align: center;
  font-size: 1.9rem;
}
.plan__boton {
  background-color: #313c4e;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #84d9bc;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.plan__boton:hover {
  cursor: pointer;
  background-color: #1d242f;
}
@media (min-width: 768px) {
  .plan__boton {
    width: auto;
    display: inline-block;
  }
}
@media (min-width: 768px) {
  .plan__boton {
    display: block;
  }
}
.plan.negocio {
  order: -1;
  background-color: #84d9bc;
  border-radius: 1rem;
  z-index: 10;
  background-image: url("../img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
@media (min-width: 768px) {
  .plan.negocio {
    order: unset;
    margin: -5rem 0;
  }
}
.plan.inicio {
  background-image: url("../img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.plan.pro {
  background-image: url("../img/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}

.testimoniales {
  padding: 8rem 0;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.testimoniales__imagen-contenedor {
  grid-column: 1/3;
}
.testimoniales__imagen {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .testimoniales__imagen {
    margin: 0;
  }
}
.testimoniales__contenido {
  grid-column: 3/6;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .testimoniales__contenido {
    margin-top: 0;
  }
}
.testimoniales__heading {
  margin-bottom: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .testimoniales__heading {
    text-align: left;
  }
}
.testimoniales__heading--span {
  font-size: 3.6rem;
  display: inline-block;
  color: #313c4e;
}
.testimoniales__heading--span::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin-top: -1.4rem;
}
.testimoniales__texto {
  border: 0.5rem solid #313c4e;
  border-radius: 1rem;
  margin: 0;
  margin-bottom: 3rem;
  padding: 2rem 0 2rem 6rem;
  background-color: #84d9bc;
  position: relative;
}
@media (min-width: 768px) {
  .testimoniales__texto {
    max-width: 70%;
  }
}
.testimoniales__texto:last-of-type {
  margin-bottom: 0;
}
.testimoniales__texto::before {
  content: "";
  display: block;
  height: 5rem;
  width: 5rem;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  position: absolute;
  left: 0.5rem;
  top: 3rem;
}
@media (min-width: 768px) {
  .testimoniales__texto:first-of-type {
    margin-left: 10%;
  }
  .testimoniales__texto:nth-child(3) {
    margin-left: 20%;
  }
  .testimoniales__texto:nth-child(4) {
    margin-left: 30%;
  }
}

.footer {
  background-color: #313c4e;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__logo {
  max-width: 15rem;
  margin: 0 auto 3rem auto;
}
@media (min-width: 768px) {
  .footer__logo {
    width: 80%;
  }
}
.footer__heading {
  font-size: 2.2rem;
  color: #FFFFFF;
  text-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.582);
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .footer__heading {
    text-align: left;
    margin-bottom: 3rem;
  }
}
.footer__box {
  margin-bottom: 4rem;
}
.footer__box:last-of-type {
  margin-bottom: 0;
}
.footer__link {
  display: block;
  color: #FFFFFF;
  font-size: 2rem;
  text-align: center;
  margin-bottom: 1rem;
}
.footer__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .footer__link {
    text-align: left;
  }
}
.footer__copyright {
  margin: 0;
  color: #FFFFFF;
  font-size: 1.3rem;
  text-align: center;
  border-top: 0.5rem solid #84d9bc;
  padding: 1rem 0;
  margin: 2rem auto 0 auto;
  max-width: 120rem;
}