@use 'base/variables' as v;
@use 'base/mixins' as m;

.crea {

    padding: 5rem 0;

    @include m.desktop {
        padding: 8rem 0;
    }

    &__grid {
        @include m.tablet {
            @include m.grid(2, 4rem);
            align-items: center;
        }
    }

    &__contenido {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    &__imagen {
        width: 80%;
        margin: 0 auto 2rem auto;
    }

    &__heading {
        @include m.headingBorder(v.$secundario, v.$primario);
    }
    
    &__texto {
        text-align: center;
        padding: 0 3rem;
    }

}